@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Acme&family=Roboto:wght@400;700&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


body {
  margin: 0;
  font-family: 'Roboto',sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  max-width: 100vw;
  background: rgba(199, 199, 197, 0.3);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section {
  width: 100%;
  height: auto;
  margin: 0 auto;
  display: grid;
  place-items: center;
  
}




a{
  text-decoration: none;
}
.header{
  width:100% ;
  height: 70px;
  top: 0;
  position: relative;
  background:#ffffff ;
  display: flex;
  align-items: center;
  justify-content:center ;
  padding: 10px;
  z-index: 100;
  font-family: "Acme";
}

.nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  position: absolute;
  z-index: 100;
  
}

.nav h1{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-left:20px ;
  color: black;
  text-transform: uppercase;
  font-style: italic;
  font-size: 2rem;
  margin-left:10px ;
  
}

@media(min-width:786px){
  .nav h1{
    width: 200px;
  }
}

ul{
  display: block;
  position: absolute;
  width: 100%;
  text-align: center;
  top: 50px;
  left: 0;
  list-style: none;
  height: auto ;
  font-size: 1.5rem;
  padding: 10px;
  background: #ffffff;
  height: 88vh;
  z-index: 99;
}

ul li{
  text-align: center;
  padding: 20px;
  color: black;
  text-transform:uppercase ;
}

.toggle{
  position: absolute;
  -webkit-transform:translateY(-150%) ;
          transform:translateY(-150%) ;

}
@media (min-width:800px){
  .toggle{
  position: relative;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
  }
}
@media (min-width:786px) {
  ul{
    display: flex;
    position: relative;
    align-items: center;
    justify-content: space-around;
    padding: 0;
    height: 50px;
    top: 0%;
    font-size: 1.2rem;
    max-width: 100px;
    visibility: visible;
  }
}

.navIcon{
  font-size: 2rem;
  margin-left: 20px;
  cursor: pointer;
  order: -1;
}

@media (min-width:786px){
  .navIcon {
    display: none;
}
}

.cartIcon{
  position: relative;
  margin-right:20px;
  width: 40px;
  height: 30px;
  color: black;
}

/* @media (min-width:786px){
  .cartIcon {
    margin-right:30px;
}
} */
.cartIcon  span{
  position: absolute;
  top: -10px;
  right: -10px;
  /* background: rgb(12, 12, 12); */
  border-radius: 50%;
  width: 100%;
  height: 100%;
  max-width: 20px;
  text-align: center;
  font-size: 1.2rem;
  color:black;
}

.cartShop{
  font-size: 2rem;
}
a{
  color: black;
}

.productContainer{
  width: 100%;
  display: grid;
  margin-top: 1rem;
  grid-template-columns: repeat(auto-fill,minmax(380px,1fr));
}



article{
  width: 90%;
  max-width: 350px;
  margin: 1rem auto;
  margin-bottom: 2rem;
  border-radius:10px ;
  background: rgb(245, 245, 245);
  box-shadow: 3px 3px 5px  rgba(199, 199, 197, 0.932),-3px -3px 5px  rgba(199, 199, 197, 0.932);
  padding-bottom: 0;
  font-family: 'Acme',sans-serif;
  letter-spacing: 0.1rem;
  
}

article h3{
  text-align: center;
  margin-top: 0.5rem ;
  font-size: 1.35rem;
}
 .product-price{
   margin: 1rem 1rem 0.5rem 1rem;
   text-align: center;
   font-size: 1.2rem;
 }

img{
  width: 100%;
  max-width: 300px;
  height:250px;
  /* margin: 0.4rem auto; */
  display: block;
  border-radius: 4px;
  object-fit: cover;
  
}

.cart-btn{
  color: white;
  background: black;
  display: inline-block;
  width: 100%;
  margin-top: 1.5rem ;
  text-align: center;
  padding: 15px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin-bottom: 0;
}
.detailContainer {
  width: 100%;
  max-width: 400px;
  border: none;
  display: grid;
  place-items:center ;
  border-radius: 10px;
  font-family: 'Acme';
  margin:2rem auto;
}


.detailImg{
  display: block;
  width: 100%;
  object-fit: cover;
  max-width: 400px;
}

.detailInfo{
  margin: 1.2rem;
  margin-bottom: 0.5rem;
}

.detailInfo .title{
  margin-top: 1.5rem;
  margin-bottom: 1rem;
  letter-spacing: 0.1rem;
}

.detailInfo .price{
  margin-bottom: 1rem;
}

.detailInfo h3{
  width: 100%;
  text-align: left;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  margin-top: 1rem;
  margin-bottom: 1rem;
  
}

.subImgContainer{
  display: none;
}

.detailColorContainer{
  display: flex;
  align-items: center;
  width: 150px;
  justify-content: space-evenly;
  max-width: 200px;
  margin-left: 2rem;
}

.singleColor{
  width: 30px;
  height: 30px;
  border-radius: 50%;
  cursor: pointer;
}

.singleColor:hover{
  border: 3px solid rgb(48, 48, 48);
  opacity: 1;
}

.detailCartBtn{
  display: inline-block;
  width: 150px;
  text-align: center;
  background: rgb(36, 36, 36);
  color: white;
  text-transform: capitalize;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 1rem;
}


/* activeBtn */
.activeBtn{
  border: 1px solid rgb(46, 45, 45);
  opacity: 1;
}


@media (min-width:900px){
  .detailContainer {
    max-width: 900px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    place-items: center;
    margin:3.5rem auto;
    min-width: 800px;
  }
  .subImgContainer{
    display: flex;
    justify-content: flex-start;
    width: 100%;
    max-height: 90px;
  }
  .subImg{
    display: inline-block;
    width: 100px;
    height: 80px;
    object-fit: contain;
    margin-right: 1rem;
    cursor: pointer;
  }

  /* .subImg:hover{
    border: 1px solid rgb(46, 45, 45);
    opacity: 1;
  }*/

} 
.cartContainer{
  width:100%;
  height: 100%;
  text-align: center;
  display: grid;
  place-items: center;
  margin:  auto;
  font-family: "Acme";
}
.cartItems{
  display: flex;
  width: 100%;
  height: 150px;
  max-width: 600px;
  align-items: center;
  justify-content: space-around;
  box-shadow: inset 0 2px 9px rgb(201, 200, 200);
  margin-top: 2rem;
  border-radius: 4px;
  background: white;
}

.cartClose{
  color: rgb(20, 19, 19);
  font-size: 1.6rem;
  cursor: pointer;
  text-align: center;
  
}

.cartItemImg img{
  width: 90px;
  height: 90px;
}

.cartInfo{
  max-width: 200px;
  min-width: 200px;
  display: flex;
  flex-direction: column;
}

.cartInfo p{
  margin-top: 0.5rem;
}

.cartBtns{
  display: flex;
  align-items: center;
  justify-content: space space-evenly;
}

.cartBtns button{
  border: none;
  outline: none;
  background: black;
  color: white;
  padding:4px 8px;
  border-radius: 5px;
  cursor: pointer;
}

.homepage-container{
  width: 100%;
  background: url('https://static.nike.com/a/images/t_PDP_1728_v1/f_auto,b_rgb:f5f5f5/6badf0f8-31cf-4e0a-b4b9-29a57be1557e/zoomx-vaporfly-next-2-womens-racing-shoe-2CMkCf.png') center/cover no-repeat;
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: "Acme";
}

.homepage-container h1{
  font-size: 3rem;
  letter-spacing: 0.3rem;
  line-height: 1;
  margin-left: 1.2rem;
  margin-bottom: 1.2rem;

}

.homepage-container p{
  max-width: 300px;
  font-weight: 400;
  margin-left: 1.2rem;
  font-size: 1rem;
}

.homepage-container a{
  display: inline-block;
  margin: 2rem;
  border: 2px solid rgb(34, 33, 33);
  max-width: 200px;
  text-align: center;
  padding: 0.6rem 2rem;
  font-size: 1rem;
  text-transform: capitalize;
  color: white;
  background: black;
  border-radius: 4px;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
}

.homepage-container a:hover{
  color: black;
  background: white;
}

.detail-icon{
  margin-left: 1rem;
  font-size: 1.5rem;
}





.contact-container{
  width: 100%;
  /* border: 2px solid green; */
  text-align: center;
  font-family:"Acme"
}

.contact-container h2{
  margin: 2rem;
  font-size: 2.5rem;
}

form{
  width: 80%;
  max-width: 400px;
  margin: auto;
  margin-bottom: 2rem;
  padding: 1rem;
  border: 2px solid rgb(150, 150, 150);
  border-radius: 4px;
  box-shadow: inset 2px 2px 6px rgb(160, 160, 160),inset -2px -2px 6px rgb(160, 160, 160);
}

form label{
  font-size: 1.5rem;
}

form input{
  width: 250px;
  height: 35px;
  border: none;
  border-bottom: 2px solid black;
  outline: none;
  background: rgb(219, 217, 217);
  padding: 12px;
  font-family: "Acme";
  font-size: 1.1rem;
}



input[type=text] {
 padding: 12px;
 background: rgb(224, 223, 223);
 font-family: "Acme";
}


input::-webkit-input-placeholder{
  font-family: "Acme";
  font-size: 1rem;
  color: black;

}


input:-ms-input-placeholder{
  font-family: "Acme";
  font-size: 1rem;
  color: black;

}


input::placeholder{
  font-family: "Acme";
  font-size: 1rem;
  color: black;

}

.form-group{
  /* border: 2px solid blue; */
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 2rem;

}

textarea {
  width: 250px;
  height: 150px;
  padding: 12px 12px;
  border: none;
  box-sizing: border-box;
  border-bottom: 3px solid rgb(15, 15, 15);
  border-radius: 4px;
  background-color:rgb(224, 223, 223);
  resize: none;
  outline: none;
  font-family: "Acme";
  font-size: 1.1rem;
  color: black;
}

.input-btn{
  font-family: "Acme";
  font-size: 1.2rem;
  padding: 5px 18px;
  cursor: pointer;
}

.input-btn{
  background: black;
  color: white;
  outline: none;
  border-radius: 4px;
}
